.header-color{
    background: #833ab4;  
    background: -webkit-linear-gradient(to bottom, #fcb045, #fd1d1d, #833ab4);  
    background: linear-gradient(to bottom, #fcb045, #fd1d1d, #833ab4); 
}
.landing-grid{
    background: #833ab4;  
    background: -webkit-linear-gradient(to bottom, #fcb045, #fd1d1d, #833ab4);  
    background: linear-gradient(to bottom, #fcb045, #fd1d1d, #833ab4);
    position: relative;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    text-align: center;
}
.demo-big-content .portifolio {
 background: black;
 color: white;
}

.projects-grid{
    display: flex;
}

.resume-right-col{
    background: #27221F;
    color: white;
    padding: 2em;
}

.contact-body{
    margin: auto;
    position: absolute;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding-top: 2em;
}
.contact-grid{
    text-align: center;
    justify-content: center;
    width: 80%;
    height: 550px;
    background: white;
    color: black;
    margin-bottom: 20px;
}
.contact-grid h2 {
    font-family: 'Anton', sans-serif;
}

.contact-grid p {
    font-family: 'Oxygen', sans-serif;
    font-weight: bold;
}
.contact-grid hr {
    border-top: 5px dotted black;
    width: 50%;
    margin: auto;
}

.contact-list i {
    font-size: 20px;
    padding-right: 3rem;
}

.avatar-img{
    height: 250px;
    padding-top: 5em;
}
.banner-text{
    background-color: black;
    opacity: 0.8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
}
.banner-text h1{
    font-size: 66px;
    font-weight: bold;
    color: white;
}
.banner-text hr {
    border-top: 5px dotted white;
    width: 50%;
    margin: auto;
}
.banner-text p{
    color: white;
    font-size:20px;
    padding: 1em;
}
.social-links{
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: auto;
}
.social-links i{
    color: white;
    font-size: 5em;
    padding: 5px;
}
